.TopNavMainContainer {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
	padding: 0.5%;
}

.TopNavigationContainer {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	width: 50%;
}

.TopNavigation {
	display: flex;
	flex-direction: row;
	/* color: white; */
	text-transform: uppercase;
	font-weight: bold;
	font-family: "Open Sans";
	font-size: 1em;
	width: 100%;
	justify-content: space-between;
	flex-wrap: wrap;
}

.TopNavigation li {
	list-style: none;
}

.TopRightContent {
	display: flex;
	/* flex-direction: row; */
	justify-content: space-around;
	width: 10%;
	flex-wrap: wrap;
}

.TopNavigationContainer a {
	text-decoration: none;
	color: black;
}

.TopNavigationContainer a:hover {
	/* text-decoration: underline; */
	color: #bd1a45;
	transition: 0.4s;
}

.TopNavigationContainer .activeTab {
	text-decoration: none;
	color: #0072ce;
}

